import './button.scss';
import { Link } from "react-router-dom";

export const Button = ({ text, handleClick, routerLinkUrl = null }) => {
    return (routerLinkUrl ?
            <Link className="button" to={routerLinkUrl}>{text}</Link>
            : (
                <div
                    className="button"
                    onClick={handleClick}
                >
                    {text}
                </div>
            )
    )
}
