import './order-page.scss';
import React, {useContext, useEffect, useReducer} from 'react';
import {useNavigate} from 'react-router-dom';
import Select from 'react-select';
import {Button} from '../button/Button';
import {Checkbox} from '../checkbox/Checkbox';
import {Radio} from '../radio/Radio';
import {FormInput} from '../form-input/FormInput';
import {ServiceItem} from '../service-item/ServiceItem';

import {Card} from '../card/Card'
import {ReactComponent as BackArrow} from '../../images/icons/back-arrow.svg';
import {LanguageContext} from '../../state/Contexts';
import {orderRU, orderCN, allRU, allCN} from '../../content/pageContent';

const ACTIONS = {
    SET_DORM: 'SET_DORM',
    SET_SERVICES: 'SET_SERVICES',
    SET_SERVICES_TO_DEFAULT: 'SET_SERVICES_TO_DEFAULT',
};

const initialState = {
    dorm: null,
    room: '',
    services: [],
    personalData: {
        name: '',
        surname: '',
        phone: '',
        email: '',
        messenger: false,
    },
    additionalService: {
        hotplateSmall: false,
        hotplateBig: false,
        lampE27: false,
    },
};

function reducer(state, action) {
    switch (action.type) {
        case ACTIONS.SET_DORM:
            return {
                ...state,
                dorm: action.payload
            };
        case ACTIONS.SET_SERVICES:
            return {
                ...state,
                services: state.services.map(item => {
                    if (action.payload.name === item.name) {
                        return {
                            ...item,
                            active: action.payload.value,
                        };
                    }
                    return item;
                })
            };
        case ACTIONS.SET_SERVICES_TO_DEFAULT:
            return {
                ...state,
                services: state.services.map(item => {
                    if (item.name === 'cleaning-for-eviction') {
                        return {
                            ...item,
                            active: true,
                        };
                    } else {
                        return {
                            ...item,
                            active: false,
                        };
                    }
                })
            };
        default:
            return state;
    }
}


export const OrderPage = () => {
    const navigate = useNavigate();
    const {language} = useContext(LanguageContext);

    let content;
    let commonContent;

    if (language === 'RU') {
        content = orderRU;
        commonContent = allRU;
    } else if (language === 'CN') {
        content = orderCN;
        commonContent = allCN;
    }

    const [state, dispatch] = useReducer(reducer, initialState, state => {
        state.services = content.services;
        state.dorm = content.addresses[0];

        return state;
    });

    useEffect(() => {
        console.log(state);
    }, [state]);

    const changeDorm = (value) => {
        if (value.value !== state.dorm.value) {
            dispatch({
                type: ACTIONS.SET_DORM,
                payload: value
            });
            dispatch({
                type: ACTIONS.SET_SERVICES_TO_DEFAULT,
            });
        }
    }

    const checkboxRadioHandler = ({name, value}) => {
        dispatch({
            type: ACTIONS.SET_SERVICES,
            payload: {name, value}
        });
    }

    const renderServices = () => {
        const dorm = state.dorm.value;
        let services = state.services;

        if (dorm !== '3' && dorm !== '7') {
            services = state.services.filter(item => item.name !== 'wall-repair');
        }

        return services.map(item => {
            return (
                <ServiceItem
                    type={'checkbox'}
                    item={item}
                    callback={checkboxRadioHandler}
                    key={item.name}
                />
            );
        })
    }

    const computedAsideTitle = () => {
        let service = state.services.filter(item => item.active).map(item => item.title).join(', ');

        return `${service}   ${state.dorm.label}   ???`;
    }

    const formInputHandler = (value, name) => {
        console.log(name, value);
    }


    return (
        <div className="order">
            <div className="wrapper">
                <div className="back-button" onClick={() => navigate(-1)}>
                    <BackArrow/> &nbsp; 
                </div>
                <div className="order__title">{content.title}</div>
                <div className="order__wrapper">
                    <div className="order__wrapper-right">
                        <Card step={' 1'} title={'   '}>
                            <div className="order__select-wrapper">
                                <Select
                                    defaultValue={state.dorm}
                                    onChange={changeDorm}
                                    placeholder={content.placeholder}
                                    options={content.addresses}
                                    styles={{
                                        control: (baseStyles) => ({
                                            ...baseStyles,
                                            borderRadius: '10px',
                                            border: 'none',
                                            cursor: 'pointer',
                                            height: '50px',
                                            fontSize: '18px',
                                            paddingLeft: '10px',
                                            maxWidth: '360px',
                                            minWidth: '200px',
                                            width: '100%',
                                        }),
                                        option: (baseStyles) => ({
                                            ...baseStyles,
                                            fontSize: '16px',
                                            cursor: 'pointer',
                                            paddingLeft: '20px',
                                        }),
                                    }}
                                />
                            </div>
                        </Card>
                        <Card step={' 2'} title={'   '}>
                            {renderServices()}
                        </Card>
                        <Card step={' 3'} title={'  '}>
                            <div className="order__form-wrapper">
                                <div className="order__form-fileds">
                                    <FormInput
                                        callback={formInputHandler}
                                        name="name"
                                        validationMessage={'error motherfucker'}
                                        value={state.personalData.name}
                                        label={'*'}
                                        validation={'valid'}
                                    />
                                </div>
                                <div className="order__form-fileds">
                                    <FormInput
                                        callback={formInputHandler}
                                        name="surname"
                                        validationMessage={'error motherfucker'}
                                        value={state.personalData.surname}
                                        label={'*'}
                                        validation={'invalid'}
                                    />
                                </div>
                            </div>
                            <textarea
                                className="order__form-textarea"
                                placeholder={'textarea placeholder'}
                                name="coment"
                            >
                            </textarea>
                        </Card>
                    </div>
                    <div className="order__wrapper-left">
                        <Card
                            title={computedAsideTitle()}
                            isAside
                        >
                        </Card>
                    </div>
                </div>
            </div>
        </div>
    )
}
